    initSelectPicker();
    initFormValidation();
    addScrolledClassToNavbarOnScroll();
    carouselsSetup(document.documentElement.getAttribute("dir") == 'rtl');
    initRecaptcha('6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI', ".grecaptcha");
